export const AdVuelidateMixin = {
  computed: {
    selectErrors() {
      const errors = [];
      if (!this.$v.select.$dirty) return errors;
      !this.$v.select.required && errors.push("Item is required");
      return errors;
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.lookup.$dirty) return errors;
      !this.$v.lookup.minLength &&
        errors.push("Must be at least 4 characters long");
      !this.$v.lookup.required && errors.push("Name is required.");
      return errors;
    }
  }
};
