<template>
  <section class="checkFeeds pt-2 mx-3">
    <page-intro :introName="introName"></page-intro>
    <v-card>
      <v-card-title>
        <v-layout align-start
                  justify-space-between
                  row
                  fill-height>
          <v-flex xs6
                  class="pr-3">
            <v-text-field clearable
                          label="Find Computers..."
                          v-model="lookup"
                          single-line
                          required
                          @input="$v.lookup.$touch()"
                          @blur="$v.lookup.$touch()"
                          @keyup.enter="GetComputers()"
                          :error-messages="nameErrors">
            </v-text-field>
          </v-flex>
          <v-flex xs3>
            <v-select v-model="select"
                      :items="items"
                      :error-messages="selectErrors"
                      label="Domain"
                      required
                      @change="$v.select.$touch()"
                      @blur="$v.select.$touch()">
            </v-select>
          </v-flex>
          <v-flex xs12>
            <div class="pl-5">
              <v-progress-circular indeterminate
                                   color="primary"
                                   class="mt-3 ml-0"
                                   v-if="loading">
              </v-progress-circular>

              <v-btn :disabled="this.$v.$invalid"
                     v-if="!loading"
                     color="tfgprimary"
                     class="mt-3 ml-0"
                     round
                     small
                     :dark="!this.$v.$invalid"
                     @click="GetComputers()">
                Find
              </v-btn>
              <exportMenu v-if="adComputers.length > 0"
                          :data="adComputers"
                          :headers="headers"
                          :colStyles="colStyles"></exportMenu>

            </div>
          </v-flex>
          <v-flex xs12>
            <v-text-field v-model="search"
                          prepend-icon="filter_list"
                          label="Filter results"
                          single-line
                          hide-details>
            </v-text-field>
          </v-flex>
        </v-layout>
      </v-card-title>

      <v-data-table :headers="headers"
                    :items="adComputers"
                    :loading=false
                    item-key="name"
                    :search="search"
                    :rows-per-page-items="rowsperpage"
                    class="px-2">

        <v-progress-linear slot="progress"
                           color="primary"
                           indeterminate
                           height="5">
        </v-progress-linear>

        <template slot="items"
                  slot-scope="props">
          <tr>
            <td class="text-xs-left pl-3">{{ props.item.name }}</td>
            <td class="text-xs-left pl-3">{{ props.item.distinguishedName }}</td>
            <td class="text-xs-left pl-3">{{ props.item.connectedServer }}</td>
            <td class="text-xs-center px-1">{{ props.item.enabled }}</td>
          </tr>
        </template>
      </v-data-table>
    </v-card>
  </section>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength } from "vuelidate/lib/validators";
import { FindADComputers } from "@/services/api/ActiveDirectory";
import { Notify } from "@/services/HelperService";
import exportMenu from "@/components/exportMenu";
import PageIntro from "@/components/PageIntro.vue";
import { DataTableMixin } from "@/components/Mixins/DataTableMixin";
import { AdVuelidateMixin } from "@/components/Mixins/AdVuelidateMixin";

export default {
  mixins: [validationMixin, DataTableMixin, AdVuelidateMixin],

  validations: {
    lookup: {
      required,
      minLength: minLength(4)
    },
    select: {
      required
    }
  },

  components: {
    exportMenu,
    PageIntro
  },

  data() {
    return {
      introName: "Active Directory Computers",
      adComputers: [],
      lookup: "",
      select: "HO",
      search: "",
      items: ["HO", "STORES", "TFGSTORES.local"],
      headers: [
        {
          text: "Name",
          align: "center",
          sortable: true,
          value: "name",
          class: "pl-1"
        },

        {
          text: "Distinguished Name",
          align: "center",
          sortable: true,
          value: "distinguishedName",
          class: "pl-1"
        },
        {
          text: "ConnectedServer",
          align: "center",
          sortable: true,
          value: "connectedServer",
          class: "pl-1"
        },
        {
          text: "Enabled",
          align: "center",
          sortable: true,
          value: "enabled",
          class: "mx-0 pr-2 pl-0"
        }
      ],
      colStyles: {
        name: {
          columnWidth: 40
        },
        distinguishedName: {
          columnWidth: "auto"
        },
        connectedServer: {
          columnWidth: 60
        },
        Enabled: {
          columnWidth: 50
        }
      }
    };
  },

  methods: {
    GetComputers() {
      this.loading = true;
      FindADComputers({ UserGroup: this.lookup, Domain: this.select })
        .then(data => {
          this.adComputers = data;
        })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }
};
</script>
